/* src/app/pages/home/home.scss */
main {
  background-color: #fff8e1;
  font-family: "Roboto", sans-serif;
}
.hero {
  background-color: rgba(255, 247, 216, 0.4941176471);
}
.carousel img {
  max-height: 500px;
  object-fit: cover;
}
.carousel-caption h2 {
  color: #d97706;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}
.carousel-caption {
  background: rgba(255, 248, 225, 0.8);
  padding: 20px;
  border-radius: 10px;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
}
.carousel-caption p {
  color: #15803d;
}
.btn-outline-warning {
  border-color: #d97706;
  color: #d97706;
}
.btn-outline-warning:hover {
  background-color: #d97706;
  color: #fff8e1;
}
.btn-warning {
  background-color: #d97706;
  border-color: #d97706;
}
.btn-warning:hover {
  background-color: #14532d;
  border-color: #14532d;
}
.products h2,
.about h2,
.blog h2,
.newsletter h2 {
  color: #d97706;
}
.card-title {
  color: #15803d;
}
.card-text {
  color: #333;
}
.card-img-top {
  height: 200px;
  object-fit: cover;
}
.text-success {
  color: #15803d !important;
}
.text-success:hover {
  color: #14532d !important;
}
@media (max-width: 767px) {
  .carousel-caption {
    display: block !important;
    background: rgba(255, 248, 225, 0.8);
    padding: 10px;
    border-radius: 5px;
  }
  .newsletter .input-group {
    width: 100% !important;
  }
}
#beeBox {
  position: absolute;
  width: 95vw;
  height: 100vh;
  overflow: hidden;
  z-index: 0;
  top: 100px;
  left: 0;
}
#bee {
  width: 125px;
  box-sizing: border-box;
  will-change: transform;
  position: absolute;
}
#beeBox1 {
  position: absolute;
  width: 95vw;
  height: 100vh;
  overflow: hidden;
  z-index: 0;
  top: 100px;
  right: 0;
}
#bee1 {
  width: 125px;
  box-sizing: border-box;
  will-change: transform;
  position: absolute;
  right: 0;
  top: 30vh;
}
.carousel-item {
  padding: 0 15px;
}
@media (min-width: 768px) {
  .carousel-item .row {
    justify-content: center;
  }
  .carousel-item .col-md-4 {
    flex: 0 0 33.333333%;
    max-width: 33.333333%;
  }
}
.carousel-control-next-icon,
.carousel-control-prev-icon {
  background-color: rgba(161, 159, 157, 0.3098039216);
  border-radius: 50%;
  margin: 10px;
  padding: 10px !important;
}
/*# sourceMappingURL=home.css.map */
